<template>
    <div class="pc-container pb-4">
        <div class="pcoded-content">
            <div class="col-xl-12 col-md-12">
                <div class="row">
                    <div class="col-sm-6 col-md-3">
                        <div class="card prod-p-card bg-primary background-pattern">
                            <div class="card-body">
                                <div class="row align-items-center m-b-0">
                                    <div class="col">
                                        <h6 class="m-b-5 text-white">Tareas</h6>
                                        <h3 class="m-b-0 text-white">10</h3>
                                    </div>
                                    <div class="col-auto">
                                        <i class="material-icons-two-tone text-white">card_giftcard</i>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="col-sm-6 col-md-3">
                        <div class="card prod-p-card bg-primary background-pattern-white">
                            <div class="card-body">
                                <div class="row align-items-center m-b-0">
                                    <div class="col">
                                        <h6 class="m-b-5 text-white">Notificaciones</h6>
                                        <h3 class="m-b-0 text-white">15</h3>
                                    </div>
                                    <div class="col-auto">
                                        <i class="material-icons-two-tone text-white">local_mall</i>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="col-sm-6 col-md-3">
                        <div class="card prod-p-card bg-primary background-pattern-white">
                            <div class="card-body">
                                <div class="row align-items-center m-b-0">
                                    <div class="col">
                                        <h6 class="m-b-5 text-white">Procesales</h6>
                                        <h3 class="m-b-0 text-white">234</h3>
                                    </div>
                                    <div class="col-auto">
                                        <i class="material-icons-two-tone text-white">monetization_on</i>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="col-sm-6 col-md-3">
                        <div class="card prod-p-card bg-primary background-pattern">
                            <div class="card-body">
                                <div class="row align-items-center m-b-0">
                                    <div class="col">
                                        <h6 class="m-b-5 text-white">Expedientes</h6>
                                        <h3 class="m-b-0 text-white">1234</h3>
                                    </div>
                                    <div class="col-auto">
                                        <i class="material-icons-two-tone text-white">local_offer</i>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

            </div>
            <div class="row">
                <div class="col-xl-12 col-md-12">
                    <div class="card table-card">
                        <div class="card-header">
                            <h5>Modificaciones en el sistema</h5>
                        </div>
                        <div class="pro-scroll">
                            <div class="card-body p-0">
                                <div class="table-responsive">
                                    <table class="table table-hover m-b-0" aria-describedby="tablaHomeAdmin">
                                        <thead>
                                            <tr>
                                                <th>Accion realizada</th>
                                                <th>Usuario</th>
                                                <th>Hora y fecha</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-for="(item, index) in expedientes" :key="index">
                                                <td v-if="item.accion!='Revisó el Expediente'">{{formatAccion(item.accion)}}</td>
                                                <td v-if="item.accion=='Revisó el Expediente'">{{formatAccion(item.accion)}}-{{item.exp.exp_numero }}</td>
                                                <td>{{item.user.name}}</td>
                                                <td>{{formatFecha(item.created_at)}}</td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
  
<script>
import { badgeClass } from '@/utils/utils.js';
import '@/assets/css/fileStatus.css';
import feather from 'feather-icons';

export default {
    name: 'admin',
    data() {
        return {
            expedientes: []
        };
    },
    computed: {
        rol() {
            return this.$store.getters.get__user.usu_rol;
        },
        mostrarOpcionAdmin() {
            return this.rol === 'ADMIN';
        },
        mostrarPanel() {
            return this.rol === 'SECRETARIA';
        }
    },
    mounted() {this.cargarExpedientes();
        this.$nextTick(() => {
            feather.replace();
        });
    },
    methods: {
        async cargarExpedientes() {
            try {
                this.$showLoadingAlert();
                this.expedientes = (await this.$postData('reportes/inicioadmin', this)).data;
            } catch (error) {
                this.$handleApiError(error, 'leer');
            } finally {
                this.$closeLoadingAlert();
            }
        },
        formatAccion(accion) {
        // Verifica que la acción no sea una cadena vacía
        if (accion && typeof accion === 'string') {
            // Convierte la primera letra a mayúscula y el resto a minúsculas
            return accion.charAt(0).toUpperCase() + accion.slice(1).toLowerCase();
        } else {
            return accion; // Devuelve la acción sin cambios si no es una cadena válida
        }
        },
        getPersona(procesal, tipo, multiple) {
            const isDemandante = procesal[0].tipo_procesal === tipo;
            return isDemandante ? this.nombreCompleto(procesal, multiple) : 'UNPRG';
        },
        nombreCompleto(procesal, multiple) {
            if (multiple === '1') {
                return 'Demanda Colectiva';
            }

            const tipoPersona = procesal[0].tipo_persona;
            const { apellido_paterno, apellido_materno, nombres, razon_social } = procesal[0];

            return tipoPersona === 'NATURAL'
                ? `${apellido_paterno} ${apellido_materno} ${nombres}`
                : razon_social;
        },
        badgeClass,
        formatFecha(fechaISO) {
            const fecha = new Date(fechaISO);
            const opcionesFormato = {
                year: 'numeric',
                month: 'short',
                day: 'numeric',
                hour: 'numeric',
                minute: 'numeric',
                second: 'numeric'
            };
            return fecha.toLocaleDateString('es-ES', opcionesFormato);
        }
    },
};
</script>
  
<style scoped>
@import '@/assets/css/dataTable.css';
</style>
  